.program {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;


  .titleEn,
  .titleRu {
	letter-spacing: 0.5rem;
	font-weight: 500;
	white-space: nowrap;
	font-family: "Great Vibes", cursive;
	text-shadow: 10px 10px 1px rgba(224, 168, 168, 0.1);
	text-align: center;
	color: #d4a373;
  }

  .titleEn {
	font-size: 5rem;
  }

  .titleRu {
	font-size: 3.1rem;
	margin-bottom: 4rem;
  }

  .item {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 3rem;
	//gap: 3px;

	.icon,
	.icons {
	  display: flex;
	  align-items: center;
	  justify-content: center;
	  width: 85px;
	  height: 85px;
	  margin-bottom: 0.6rem;
	}

	.time {
	  font-size: 1.7rem;
	  font-weight: bold;
	}

	.text {
	  font-size: 1.7rem;
	}
  }

  @media (max-width: 1680px) {
	.titleEn {
	  font-size: 4.7rem;
	}
	.titleRu {
	  font-size: 2.7rem;
	}
	.item {
	  .icon,
	  .icons {
		width: 75px;
		height: 80px;
	  }

	  .time,
	  .text {
		font-size: 1.5rem;
	  }
	}
  }

  @media (max-width: 1024px) {
	.titleEn {
	  font-size: 4.5rem;
	}
	.titleRu {
	  font-size: 2.4rem;
	}
	.item {
	  .icon,
	  .icons {
		width: 65px;
		height: 70px;
	  }

	  .time,
	  .text {
		font-size: 1.5rem;
	  }
	}
  }

  @media (max-width: 768px) {
	.titleEn {
	  font-size: 3.7rem;
	  white-space: normal;
	  word-break: break-word;
	}
	.titleRu {
	  font-size: 2.2rem;
	}
	.item {
	  .icon,
	  .icons {
		width: 60px;
		height: 65px;
	  }

	  .time,
	  .text {
		font-size: 1.4rem;
	  }
	}
  }

  @media (max-width: 451px) {
	.titleEn {
	  font-size: 3.5rem;
	  white-space: normal;
	  word-break: break-word;
	  letter-spacing: 0.3rem;
	  font-weight: 500;
	}
	.titleRu {
	  font-size: 2.8rem;
	  margin-bottom: 50px;
	}
	.item {

	  .icon,
	  .icons {
		width: 65px;
		height: 65px;
	  }

	  .time,
	  .text {
		font-size: 1.5rem;
	  }
	}
  }

  @media (max-width: 375px) {
	.titleEn {
	  font-size: 3.3rem;
	}
	.titleRu {
	  font-size: 3rem;
	}
	.item {
	  margin-bottom: 3.5rem;

	  .icon,
	  .icons {
		width: 65px;
		height: 65px;
	  }

	  .time,
	  .text {
		font-size: 1.5rem;
	  }
	}
  }

  @media (max-width: 320px) {
	.titleEn {
	  //font-size: 3.3rem;
	}
	.titleRu {
	  //font-size: 4rem;
	}
	.item {
	  //gap: 3px;

	  .icon,
	  .icons {
		width: 55px;
		height: 55px;
	  }

	  .time,
	  .text {
		font-size: 1.5rem;
	  }
	}
  }
}

.halfBorder {
  height: 1px;
  width: 90%;
  margin: 50px auto;
  background: linear-gradient(to right, transparent, #1f1a1a, transparent);
  opacity: 0.6;
  border-radius: 50%;
}
